import store from '@/store';
import {Dialog} from 'we-vue'

(function (w) {
  var checkUrl = 'http://s.55duanzi.com/rebate/js/update.json?v=' + Date.parse(new Date()); //  
  var downloadWgtUrl = null; // 
  var downloadApkUrl = null; // 
  var iosURL = null; // 
  var oldVer = null; // 
  var newVer = null; // 
  var isios = null; // ios 1  2 appStore 3 
  var isandroid = null; // android 1  2 apk 3 
  var channel = null; // 
  // plusReady 
  function plusReady() {
    //
    // plus.navigator.setStatusBarBackground('#e53625');
    // IMEI
    // console.log( "IMEI: " + plus.device.imei );
    // localStorage.setItem('imei', plus.device.uuid.split(',')[0]);
    store.state.deviceuuid = plus.device.uuid.split(',')[0];
    store.state.devicemodel = plus.device.model;
    // store.state.platForm = plus.os.name.toLowerCase();
    // 
    plus.runtime.getProperty(plus.runtime.appid, function (inf) {
      oldVer = inf.version; //
      localStorage.setItem('version', inf.version);//
      // store.state.version = inf.version; //
      //
      // let v = inf.version;
      // v = v.split('_', 1).join('.');
      // store.state.aboutVersion = v;

      //
      checkArguments();

      channel = oldVer.split('_')[1];
      console.log(oldVer);
      checkUpdate();
    });
    //
    var first = null;
    plus.key.addEventListener("backbutton", function () {
      var otherView = plus.webview.getWebviewById('taobao') || null;
      if (otherView) {
        otherView.canBack(function (e) {
          if (e.canBack) {
            otherView.back();//
          } else {
            plus.webview.close(otherView);
          }
        });
      } else {
        var fileHref = window.location.href;
        console.log(fileHref);
        var reg = RegExp(/#\/(index|collect|mine)/g);
        if (reg.test(fileHref)) {
          //
          if (!first) {
            first = new Date().getTime();//
            plus.nativeUI.toast('');//
            setTimeout(function () {//1s
              first = null;
            }, 1000);
          } else {
            if (new Date().getTime() - first < 1000) {//1s
              plus.runtime.quit();//app
            }
          }
        } else {
          history.go(-1)//
        }
      }
    });
    //url
    // var currentView=plus.webview.getWebviewById('index');
    // currentView.overrideUrlLoading({mode:"reject",match:".*sogou\.com/.*"}, function (e) {
    //     var url=e.url;
    //     var w = plus.webview.open(url, 'ad');
    // });
    //
    document.addEventListener('newintent', function () {
      console.log('');
      checkArguments();
    }, false);
  }

  // 
  function checkUpdate() {
    var xhr = new XMLHttpRequest();
    xhr.onreadystatechange = function () {
      switch (xhr.readyState) {
        case 4:
          if (xhr.status == 200) {
            var res = eval('(' + xhr.responseText + ')');
            // 
            newVer = res.version; // 
            console.log(newVer);
            isios = res.ios; // ios 1  2 appStore 3 
            iosURL = res.ipaURL;
            downloadWgtUrl = res.wgtURL;
            downloadApkUrl = res.apkURL[channel];
            // console.log(downloadApkUrl);
            isandroid = res.android; // android 1  2 apk 3 

            // 
            if (plus.navigator.hasSplashscreen()) { // 
              document.addEventListener('splashclosed', checkOs, false);
            } else { //
              checkOs();
            }
          } else {
            console.log('');
          }
          break;
        default:
          break;
      }
    };
    xhr.open('GET', checkUrl);
    xhr.send();
  }

  //
  function checkOs() {
    var isupdate = compareVersion(oldVer, newVer); // 
    console.log('' + isupdate);
    if (!isupdate) {
      return false;
    }
    ;
    if (plus.os.name == 'Android') {    // Android 
      if (isandroid == 1) {
        return false;
      } else if (isandroid == 2) {
        Dialog.confirm({
          message: '!',
          skin: 'android',
          showCancelButton: true
        }).then(() => {
          console.log('ok');
          plus.nativeUI.showWaiting("...");
          createDownload();
        }).catch(() => {
          console.log('cannel');
        })
      } else {
        downWgt();
      }
    } else { // 
      if (isios == 1) {
        return false;
      } else if (isios == 2) {
        plus.nativeUI.alert('!', function () {
          plus.runtime.openURL(iosURL);
        }, '', '');
      } else {
        downWgt();
      }
    }
  }

  /**
   * nvovtruefalse
   * @param {String} ov
   * @param {String} nv
   * @return {Boolean}
   */
  function compareVersion(ov, nv) {
    if (!ov || !nv || ov == '' || nv == '') {
      return false;
    }
    var ovNum = ov.split('_')[0];
    var b = false,
      ova = ovNum.split('.', 4),
      nva = nv.split('.', 4);
    for (var i = 0; i < ova.length && i < nva.length; i++) {
      var so = ova[i], no = parseInt(so), sn = nva[i], nn = parseInt(sn);
      if (nn > no || sn.length > so.length) {
        return true;
      } else if (nn < no) {
        return false;
      }
    }
    if (nva.length > ova.length && 0 == nv.indexOf(ov)) {
      return true;
    }
  }

  // wgt
  function downWgt() {
    plus.nativeUI.showWaiting('...');
    plus.downloader.createDownload(downloadWgtUrl, {filename: '_doc/update/'}, function (d, status) {
      if (status == 200) {
        console.log('wgt' + d.filename);
        installWgt(d.filename); // wgt
      } else {
        console.log('wgt');
      }
      plus.nativeUI.closeWaiting();
    }).start();
  }

  // 
  function installWgt(path) {
    // console.log(path);
    plus.nativeUI.showWaiting('...');
    plus.runtime.install(path, {}, function () {
      plus.nativeUI.closeWaiting();
      plus.nativeUI.toast('');
      plus.runtime.restart();
    }, function (e) {
      plus.nativeUI.closeWaiting();
      console.log(e);
      // plus.nativeUI.alert(JSON.stringify(e));
      plus.nativeUI.toast('');
    });
  }

  // 
  function createDownload() {
    var dtask = plus.downloader.createDownload(downloadApkUrl, {filename: '_doc/download/'}, function (d, status) {
      // 
      plus.nativeUI.closeWaiting();
      if (status == 200) {
        plus.runtime.install(d.filename, {}, function () {
        }, function (DOMException) {
          console.log(JSON.stringify(DOMException));
        });
      } else {
        plus.nativeUI.toast('');
      }
    });
    dtask.start();
  }

  //
  function checkArguments() {
    var args = plus.runtime.arguments;
    if (args && args.indexOf('koudai://') == 0) {
      // args
      let url = args.replace(/koudai:\/\//g, '');
      let currentUrl = plus.webview.currentWebview().getURL().split('/');
      let currentUrlLen = currentUrl.length;
      currentUrl[currentUrlLen - 1] = url;
      window.location.href = currentUrl.join('/');
    }
    //&&
    // uploadService();
  }

  //&&
  function uploadService() {
    let uuid = JSON.parse(localStorage.getItem('kdtt')) != null ? JSON.parse(localStorage.getItem('kdtt')).uuid : '';
    $.ajax({
      url: 'http://api.55duanzi.com/tt/api/user/time',
      type: 'get',
      headers: {
        'uuid': uuid
      },
      success: function (res) {
        var time = Date.parse(new Date()).toString().slice(0, 10);
        var differ = res.timestamp - time;
        localStorage.setItem('timestamp', differ);
        console.log('' + localStorage.getItem('timestamp'));
      },
      error: function () {
        localStorage.setItem('timestamp', 0);
      }
    })
    let version = localStorage.getItem('version').split('_');
    let imei = localStorage.getItem('imei');
    let uid = localStorage.getItem('kdtt') != null ? 'USR_' + JSON.parse(localStorage.getItem('kdtt')).uuid : 'GUEST_' + imei;
    $.ajax({
      url: 'http://api.55duanzi.com/z',
      type: 'get',
      data: {
        app_version: version[0],
        app_store: version[1],
        imei: imei,
        uid: uid,
        action: '15',
      }
    });
  }

  if (window.plus) {
    plusReady();
  } else {
    document.addEventListener('plusready', plusReady, false);
  }
})(window);



