export default [
  {
    path: '/',
    name: 'index',
    redirect: {path: '/index'}
  },
  {
    meta: {
      title: '',
      keepAlive: false,
      isBack: false,
    },
    path: '/index',
    name: 'index',
    component: resolve => require(['views/list'], resolve),
  },
  {
    meta: {
      title: '',
      keepAlive: false,
      isBack: false,
    },
    path: '/more',
    name: 'more',
    component: resolve => require(['views/more'], resolve),
  },
  {
    meta: {
      title: '',
      keepAlive: false,
      isBack: false,
    },
    path: '/detail',
    name: 'detail',
    component: resolve => require(['views/detail'], resolve),
  },
  {
    meta: {
      title: '',
      keepAlive: false,
      isBack: false,
    },
    path: '/collect',
    name: 'collect',
    component: resolve => require(['views/collect'], resolve),
  },
  {
    meta: {
      title: '',
      keepAlive: false,
      isBack: false,
    },
    path: '/mine',
    name: 'mine',
    component: resolve => require(['views/mine'], resolve),
  },
  {
    meta: {
      title: '',
      keepAlive: false,
      isBack: false,
    },
    path: '/login',
    name: 'login',
    component: resolve => require(['views/login'], resolve),
  },

];



